
* {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
body {
  background: #333;
  font-size: 13px;
  font-family: Helvetica,Arial,sans-serif;
}
a {
  text-decoration: none;
}

.home-container {
  width: 100%;
  height: 100%;
  display: relative;
  -webkit-animation: mainEnter 1s;
          animation: mainEnter 1s;
}
.home-container a {
    position: absolute;
    display: block;
    width: 100px;
    height: 100px;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    border: 1px solid dodgerblue;
    border-radius: 100px;
    text-align: center;
    line-height: 100px;
    font-size: 18px;
    color: dodgerblue;
}
.home-container a:hover, .home-container a:active {
      background: #444;
}
.home-container .circle {
    position: absolute;
    display: block;
    width: 100px;
    height: 100px;
    border-radius: 100px;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    border: 1px solid dodgerblue;
}
.home-container .circle-ani {
    -webkit-animation: enter 2s infinite;
            animation: enter 2s infinite;
}
.home-container .title {
    color: dodgerblue;
    text-align: center;
    position: fixed;
    bottom: 50px;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    font-weight: lighter;
}
@-webkit-keyframes enter {
0% {
    -webkit-transform: translate(-50%, -50%) scale(1, 1);
            transform: translate(-50%, -50%) scale(1, 1);
    opacity: 1;
}
100% {
    -webkit-transform: translate(-50%, -50%) scale(1.4, 1.4);
            transform: translate(-50%, -50%) scale(1.4, 1.4);
    opacity: 0;
}
}
@keyframes enter {
0% {
    -webkit-transform: translate(-50%, -50%) scale(1, 1);
            transform: translate(-50%, -50%) scale(1, 1);
    opacity: 1;
}
100% {
    -webkit-transform: translate(-50%, -50%) scale(1.4, 1.4);
            transform: translate(-50%, -50%) scale(1.4, 1.4);
    opacity: 0;
}
}
@-webkit-keyframes mainEnter {
0% {
    opacity: 0;
}
100% {
    opacity: 1;
}
}
@keyframes mainEnter {
0% {
    opacity: 0;
}
100% {
    opacity: 1;
}
}

.vue-container {
  max-width: 700px;
  margin: 0 auto;
  background: #fafafa;
  color: #333;
}
.vue-container a {
    color: #333;
    text-decoration: none;
}
.vue-container .divider {
    background: #ddd;
    height: 1px;
}
.vue-container .divider.divider-short {
      max-width: 40px;
      background: #333;
}
.vue-container .vue-player {
    width: 100%;
    height: 100%;
    position: relative;
}
.vue-container .vue-player > video {
      width: 100%;
      height: 100%;
}
.vue-container .vue-player .cover {
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      background-size: cover;
      background-position: 50%;
}
.vue-container .vue-player .icon-play {
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      background-size: 60px 60px;
      background-position: 50%;
      background-repeat: no-repeat;
      background-color: rgba(0, 0, 0, 0.2);
      cursor: pointer;
      -webkit-transition: background-color .5s;
      transition: background-color .5s;
}
.vue-container .vue-player .icon-play:hover {
        background-color: rgba(0, 0, 0, 0.5);
}
.vue-container .vue-video-info {
    width: 100%;
}
.vue-container .vue-video-info .video-meta {
      padding: 15px;
}
.vue-container .vue-video-info .video-meta > h1 {
        font-size: 18px;
        margin-bottom: 7px;
}
.vue-container .vue-video-info .video-meta .divider {
        margin-bottom: 7px;
}
.vue-container .vue-video-info .video-meta > p {
        margin-bottom: 20px;
}
.vue-container .vue-video-info .video-meta .description {
        opacity: .5;
}
.vue-container .video-nav {
    background: #fff;
    padding: 0px 40px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: distribute;
        justify-content: space-around;
}
.vue-container .video-nav .tab {
      padding: 10px 20px;
}
.vue-container .video-nav .tab.active {
        border-bottom: 3px solid dodgerblue;
}
.vue-container .video-nav .tab.active a, .vue-container .video-nav .tab a:hover {
        color: dodgerblue;
}
.vue-container .video-nav .tab.active a .comment-count, .vue-container .video-nav .tab a:hover .comment-count {
          color: dodgerblue;
          border: 1px solid dodgerblue;
}
.vue-container .video-nav .tab .comment-count {
        padding: 0 6px;
        margin-left: 6px;
        border: 1px solid #bbb;
        border-radius: 20px;
        color: #bbb;
        font-size: 10px;
}
.vue-container .meta-container {
    position: relative;
    width: 100%;
    height: 1200px;
    overflow: hidden;
}
.vue-container .meta-container .relate-video-list {
      background: #fafafa;
      position: absolute;
      padding: 20px;
      width: 100%;
}
.vue-container .meta-container .relate-video-list .relate-video {
        margin-bottom: 20px;
        position: relative;
}
.vue-container .meta-container .relate-video-list .relate-video a {
          display: block;
}
.vue-container .meta-container .relate-video-list .relate-video a:active {
            background: #f2f2f2;
}
.vue-container .meta-container .relate-video-list .relate-video .relate-cover {
          width: 40%;
          overflow: hidden;
}
.vue-container .meta-container .relate-video-list .relate-video .relate-cover > img {
            width: 100%;
            height: auto;
}
.vue-container .meta-container .relate-video-list .relate-video .relate-meta {
          position: absolute;
          left: 40%;
          margin-left: 15px;
          top: 50%;
          -webkit-transform: translateY(-50%);
                  transform: translateY(-50%);
}
.vue-container .meta-container .relate-video-list .relate-video .relate-meta .title {
            margin-bottom: 5px;
}
.vue-container .meta-container .relate-video-list .relate-video .relate-meta .category {
            opacity: .5;
}
.vue-container .tags-container {
    text-align: center;
    padding: 20px 0;
}
.vue-container .tags-container header {
      margin-bottom: 20px;
}
.vue-container .tags-container .tag {
      display: inline-block;
      background: #ccc;
      margin: 0 4px;
      padding: 2px 10px 4px 10px;
      border-radius: 2px;
      color: #fff;
}
.vue-container .footer {
    text-align: center;
    padding: 30px 0;
    opacity: .5;
}
.slide-left-leave-active, .slide-left-enter-active {
  -webkit-transition: all 1s cubic-bezier(0.55, 0, 0.1, 1);
  transition: all 1s cubic-bezier(0.55, 0, 0.1, 1);
}
.slide-left-leave-to {
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
}
.slide-left-enter {
  -webkit-transform: translateX(100%);
          transform: translateX(100%);
}
.slide-right-leave-active, .slide-right-enter-active {
  -webkit-transition: all 1s cubic-bezier(0.55, 0, 0.1, 1);
  transition: all 1s cubic-bezier(0.55, 0, 0.1, 1);
}
.slide-right-leave-to {
  -webkit-transform: translateX(100%);
          transform: translateX(100%);
}
.slide-right-enter {
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
}
.icon-play {
  background-image: url(data:image/png;base64,);
}

.video-comment-list {
  padding-top: 30px;
  font-size: 12px;
  background: #fafafa;
  position: absolute;
  width: 100%;
}
.video-comment-list .comment {
    margin-bottom: 20px;
}
.video-comment-list .comment .avatar {
      width: 40px;
      height: 40px;
      border-radius: 40px;
      background: #ddd;
      float: left;
      margin-left: 20px;
      overflow: hidden;
      position: relative;
}
.video-comment-list .comment .avatar img {
        width: 100%;
        height: auto;
        display: block;
        position: absolute;
        top: 50%;
        -webkit-transform: translateY(-50%);
                transform: translateY(-50%);
}
.video-comment-list .comment .meta {
      margin-left: 70px;
      position: relative;
}
.video-comment-list .comment .meta .user-name {
        margin-bottom: 4px;
}
.video-comment-list .comment .meta .time {
        margin-bottom: 4px;
        opacity: .85;
}
.video-comment-list .comment .meta .text {
        margin-bottom: 20px;
        padding-right: 20px;
}
.video-comment-list .comment .meta .like {
        position: absolute;
        right: 20px;
        top: 0;
        text-align: right;
}
.video-comment-list .comment .meta .like .like-count img {
          width: 15px;
          height: 15px;
          margin-left: 3px;
}
